import PaymentIcon from "@mui/icons-material/Payment";
import {
  Box,
  Button,
  Container,
  CssBaseline,
  Stack,
  ThemeProvider,
  Typography,
  createTheme,
} from "@mui/material";
import { Card, Payments } from "@square/web-payments-sdk-types";
import { payments } from "@square/web-sdk";
import { useEffect, useMemo, useState } from "react";
import { unstable_batchedUpdates } from "react-dom";
import WebFont from "webfontloader";
import { ReceivingWindowMessage } from "./WindowMessage";

const incomingMessageTypeString = "square_create_card_form_incoming";
const outgoingMessageTypeString = "square_create_card_form_outgoing";

function App() {
  const [paymentsState, setPaymentsState] = useState<Payments | null>(null);
  const [cardState, setCardState] = useState<Card | null>(null);
  const [buttonTextState, setButtonTextState] = useState<string | undefined>(
    undefined
  );
  const [fontFamilyState, setFontFamilyState] = useState<string | undefined>(
    undefined
  );
  const [prefersDarkState, setPrefersDarkState] = useState<boolean | undefined>(
    undefined
  );
  const [themeColorState, setThemeColorState] = useState<string>("#000");
  const [consentTextState, setConsentTextState] = useState<string>("");
  const [squareApplicationIdState, setSquareApplicationIdState] = useState<
    string | undefined
  >(undefined);
  const [squareLocationIdState, setSquareLocationIdState] = useState<
    string | undefined
  >(undefined);
  const fontFamilyString = `${fontFamilyState}, sans-serif`;

  const loaded = buttonTextState !== undefined;

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode: prefersDarkState ? "dark" : "light",
          primary: {
            main: themeColorState,
          },
        },
        typography: {
          fontFamily: fontFamilyString,
        },
      }),
    [fontFamilyString, prefersDarkState, themeColorState]
  );

  useEffect(() => {
    if (fontFamilyState) {
      WebFont.load({
        google: {
          families: [fontFamilyState],
        },
      });
    }
  }, [fontFamilyState]);

  useEffect(() => {
    async function initializePayments() {
      let loadedPayments: Payments | null;
      if (!paymentsState && squareApplicationIdState && squareLocationIdState) {
        try {
          loadedPayments = await payments(
            squareApplicationIdState,
            squareLocationIdState
          );
          if (loadedPayments != null) {
            setPaymentsState(loadedPayments);
          }
        } catch (error) {
          console.error(error);
        }
      }
    }
    initializePayments();
  }, [paymentsState, squareApplicationIdState, squareLocationIdState]);

  useEffect(() => {
    let card: Card | undefined;
    if (paymentsState && prefersDarkState !== undefined && !cardState) {
      try {
        (async () => {
          card = await paymentsState.card(
            prefersDarkState
              ? {
                  style: {
                    ".input-container": {
                      borderColor: "#2D2D2D",
                    },
                    ".input-container.is-focus": {
                      borderColor: "#006AFF",
                    },
                    ".input-container.is-error": {
                      borderColor: "#ff1600",
                    },
                    ".message-text": {
                      color: "#999999",
                    },
                    ".message-icon": {
                      color: "#999999",
                    },
                    ".message-text.is-error": {
                      color: "#ff1600",
                    },
                    ".message-icon.is-error": {
                      color: "#ff1600",
                    },
                    input: {
                      backgroundColor: "#2D2D2D",
                      color: "#FFFFFF",
                    },
                    "input::placeholder": {
                      color: "#999999",
                    },
                    "input.is-error": {
                      color: "#ff1600",
                    },
                  },
                }
              : undefined
          );
          await card.attach("#card-container");
          if (card != null) {
            setCardState(card);
          }
        })();
      } catch (error) {
        console.error(error);
      }
    }

    return () => {
      cardState?.detach();
    };
  }, [paymentsState, cardState, prefersDarkState]);

  useEffect(() => {
    function handleIncomingMessage(
      event: MessageEvent<
        ReceivingWindowMessage<{
          buttonText?: string;
          fontFamily?: string;
          prefersDark?: string;
          themeColor?: string;
          squareApplicationId?: string;
          squareLocationId?: string;
          consentText?: string;
        }>
      >
    ) {
      try {
        const data = event.data.data;
        const payload = data.payload;

        if (data.type === incomingMessageTypeString) {
          unstable_batchedUpdates(() => {
            if (payload.buttonText) {
              setButtonTextState(payload.buttonText);
            }
            if (payload.fontFamily) {
              setFontFamilyState(payload.fontFamily);
            }
            if (payload.prefersDark) {
              setPrefersDarkState(payload.prefersDark === "true");
            }
            if (payload.themeColor) {
              setThemeColorState(payload.themeColor);
            }
            if (payload.squareApplicationId) {
              setSquareApplicationIdState(payload.squareApplicationId);
            }
            if (payload.squareLocationId) {
              setSquareLocationIdState(payload.squareLocationId);
            }
            if (payload.consentText) {
              setConsentTextState(payload.consentText);
            }
          });
        }
      } catch (e) {
        console.error("Failed to parse message:", e);
      }
    }
    window.addEventListener("message", handleIncomingMessage);

    window.parent.postMessage(
      {
        type: outgoingMessageTypeString,
        payload: "update",
      },
      window.parent.origin
    );

    return () => {
      window.removeEventListener("message", handleIncomingMessage);
    };
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container sx={{ pt: 2 }}>
        <Box justifyContent="center" display="flex">
          <Stack gap={1} width={"100%"} maxWidth={"400px"} textAlign={"center"}>
            <form id="payment-form">
              <div id="card-container" style={{ minHeight: "97px" }}></div>
              {loaded && (
                <Button
                  sx={{ borderRadius: 5 }}
                  variant="contained"
                  id="card-button"
                  fullWidth
                  startIcon={<PaymentIcon />}
                  onClick={async () => {
                    const cardTokenizationResult = await cardState?.tokenize();
                    if (
                      cardTokenizationResult?.status !== "OK" ||
                      cardTokenizationResult?.token == null
                    ) {
                      throw new Error(
                        `Tokenization errors: ${JSON.stringify(
                          cardTokenizationResult?.errors
                        )}`
                      );
                    }

                    const verifyBuyerResponseDetails =
                      await paymentsState?.verifyBuyer(
                        cardTokenizationResult?.token,
                        {
                          intent: "STORE",
                          billingContact: {},
                        }
                      );

                    window.parent.postMessage(
                      {
                        type: outgoingMessageTypeString,
                        payload: {
                          sourceId: cardTokenizationResult?.token,
                          verificationToken: verifyBuyerResponseDetails?.token,
                        },
                      },
                      window.parent.origin
                    );
                  }}
                >
                  {buttonTextState}
                </Button>
              )}
            </form>
            <Typography pt="2" variant="caption">
              {consentTextState}
            </Typography>
          </Stack>
        </Box>
      </Container>
    </ThemeProvider>
  );
}

export default App;
